'use client';
import React from 'react';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import FitnessCenterIcon from '@mui/icons-material/FitnessCenter';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import PeopleIcon from '@mui/icons-material/People';
import LocalFireDepartmentIcon from '@mui/icons-material/LocalFireDepartment';
import MonitorHeartIcon from '@mui/icons-material/MonitorHeart';
import SportsGymnasticsIcon from '@mui/icons-material/SportsGymnastics';

const Features = () => {
  const features = [
    {
      icon: <FitnessCenterIcon sx={{ fontSize: 40, color: 'primary.main' }} />,
      title: 'Modern Equipment',
      description: 'State-of-the-art gym equipment for all your fitness needs',
    },
    {
      icon: <AccessTimeIcon sx={{ fontSize: 40, color: 'primary.main' }} />,
      title: '24/7 Access',
      description: 'Work out anytime with round-the-clock gym access',
    },
    {
      icon: <PeopleIcon sx={{ fontSize: 40, color: 'primary.main' }} />,
      title: 'Expert Trainers',
      description: 'Professional trainers to guide your fitness journey',
    },
    {
      icon: <LocalFireDepartmentIcon sx={{ fontSize: 40, color: 'primary.main' }} />,
      title: 'Fat Burning',
      description: 'Specialized programs for effective weight loss',
    },
    {
      icon: <MonitorHeartIcon sx={{ fontSize: 40, color: 'primary.main' }} />,
      title: 'Health Monitoring',
      description: 'Track your progress with advanced health metrics',
    },
    {
      icon: <SportsGymnasticsIcon sx={{ fontSize: 40, color: 'primary.main' }} />,
      title: 'Group Classes',
      description: 'Join group fitness classes for motivation and fun',
    },
  ];

  return (
    <Box sx={{ py: 4, backgroundColor: 'background.paper' }}>
      <Container maxWidth="lg">
        <Box sx={{ textAlign: 'center', mb: 4 }}>
          <Typography variant="h3" component="h2" gutterBottom sx={{ fontWeight: 'bold' }}>
            Why Choose Our Gym
          </Typography>
          <Typography variant="h6" color="text.secondary" sx={{ maxWidth: 600, mx: 'auto' }}>
            We provide everything you need to achieve your fitness goals in a supportive environment
          </Typography>
        </Box>

        <Grid container spacing={4}>
          {features.map((feature, index) => (
            <Grid item xs={12} sm={6} md={4} key={index}>
              <Card 
                sx={{ 
                  height: '100%', 
                  textAlign: 'center',
                  transition: 'transform 0.3s ease-in-out',
                  '&:hover': {
                    transform: 'translateY(-8px)',
                    boxShadow: 4,
                  },
                }}
              >
                <CardContent sx={{ p: 3 }}>
                  <Box sx={{ mb: 2, display: 'flex', justifyContent: 'center' }}>
                    {feature.icon}
                  </Box>
                  <Typography variant="h5" component="h3" gutterBottom sx={{ fontWeight: 'bold' }}>
                    {feature.title}
                  </Typography>
                  <Typography variant="body1" color="text.secondary">
                    {feature.description}
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>

        <Box sx={{ textAlign: 'center', mt: 6 }}>
          <Button
            variant="contained"
            size="large"
            sx={{
              px: 4,
              py: 1.5,
              fontSize: '1.1rem',
            }}
          >
            Join Now
          </Button>
        </Box>
      </Container>
    </Box>
  );
};

export default Features;